import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import About from './views/About.vue';
import Yum from './views/Yum.vue';
import Robotask from './views/Robotask.vue';
import Giraffe from './views/Giraffe.vue';
import Chairo from './views/Chairo.vue';
import Kamchatka from './views/Kamchatka.vue';
import Jewels from './views/Jewels.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior(to) {
    if (to.hash) {
      document.getElementById(to.hash.replace('#', '')).scrollIntoView({ behavior: 'smooth' });
      return {};
      // return new Promise((resolve) => {
      //   setTimeout(() => {
      //     resolve({ selector: to.hash });
      //   }, 10000);
      // });
      // return { selector: to.hash };
    }
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      alias: '/#work',
    },
    {
      path: '/about',
      name: 'about',
      component: About,
    },
    {
      path: '/work/yum',
      name: 'yum',
      component: Yum,
    },
    {
      path: '/work/robotask',
      name: 'robotask',
      component: Robotask,
    },
    {
      path: '/work/giraffe',
      name: 'giraffe',
      component: Giraffe,
    },
    {
      path: '/work/chairo',
      name: 'chairo',
      component: Chairo,
    },
    {
      path: '/work/jewels',
      name: 'jewels',
      component: Jewels,
    },
    {
      path: '/work/kamchatka',
      name: 'kamchatka',
      component: Kamchatka,
    },
  ],
});
